.App {
  text-align: center;
  min-height: 100%;

}
.flex-column{
  align-items: center;
}
.teamPicture{
  vertical-align: top;
  display: inline-block;
  padding-left: 15px;
  padding-right: 15px;
}
.row{
  justify-content:center;
  align-items: center;
  display: flex;
  align-content: space-between;
  text-align: center;

}

.teamRows{
  margin:50px;
}

.footer{
  margin-top: 30px;
  max-width: 100%;
  bottom: 0;
  width: 100%;
  /*background-color: #205c30;*/
  background-color: #F8F9FA;
  box-sizing: border-box;
  align-content: center;
}
.footerLinks{
  text-align: left;
}
.footerContents{
  margin-top: 30px;
  margin-bottom: 30px;
  align-content: start;
  text-align: left;
  padding-left: 30px;
  padding-right: 30px;
}
.footerLogo{
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.footerLinksContent{
  color:#7C7C7D ;
}
.copyrightLine{
  padding-bottom: 20px;
}
.socialMediaIcons{
  margin-right: 10px;
  margin-top: 5px;
}
.App-logo {
height: 40vmin;
pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
.App-logo {
animation: App-logo-spin infinite 20s linear;
}
}

.App-header {
background-color: #282c34;
min-height: 100vh;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
font-size: calc(10px + 2vmin);
color: white;
}

.App-link {
color: #61dafb;
}

@keyframes App-logo-spin {
from {
transform: rotate(0deg);
}
to {
transform: rotate(360deg);
}
}

.topNavBar{
margin-bottom: 20px;
}

